<template>
      <div class="BAR u-cf">
        <div class="wrapper">
          <div class="nav_LOGO">
            <a @click="$router.push({ name: 'home'})">Vá ao café</a>
          </div>
          <ul class="nav_menu">
            <li>
              <a @click="$router.push({ name: 'area'})">搜尋</a>
            </li>
            <li :style="{display:this.like}">
              <a href="#">收藏</a>
            </li>
            <li :style="{display:this.signin}" >
              <a @click="$router.push({ name: 'login'})">登入</a>
            </li>
            <li :style="{ display:this.signout}">
              <a  @click="logout">登出</a>
            </li>
          </ul>
        </div>
      </div>
</template>

<script>
import firebase, { functions } from "firebase";
import router from "../router.js";

export default {
    name:"Bar",
    data(){
      return{
      signout: "none",
      signin: "",
      like: "none"
      }
    },
    methods:{
      async logout() {
      firebase
        .auth()
        .signOut()
        .then(function() {
          firebase.auth().onAuthStateChanged(function(user) {
            if (user) {
              console.log("yes");
            } else {
              console.log("no");
            }
          });
        });
        this.signout="none";
        this.signin="";
        this.like="none";
        this.setState();
    },
    setState() {
      sessionStorage.setItem("signIn", this.signin);
      sessionStorage.setItem("signOut", this.signout);
      sessionStorage.setItem("like", this.like);
    }
    },
    mounted() {
    this.signout =sessionStorage.getItem("signOut")
    this.signin =sessionStorage.getItem("signIn")
    this.like =sessionStorage.getItem("like")
  },
}
</script>

<style scoped>
@charset "UTF-8";
* {
  margin: auto;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  font-family: arial, "Microsoft JhengHei", "微軟正黑體", sans-serif !important;
}

*::selection {
  color: #fff;
    	background: rgba(255, 255, 255, 0);

}

.u-cf:after {
  content: "";
  display: block;
  clear: both;
}

.BAR {
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 15;
}

.wrapper {
  width: 90%;
  margin: 0 auto;
}

.nav_LOGO a {
  text-decoration: none;
  float: left;
  font-weight: bold;
  color: #fff;
  font-size: 32px;
  margin-top: 2px;
}

.nav_menu {
  float: right;
  padding: 15px 0;
  font-size: 16px;
}

.nav_menu li {
  list-style: none;
  float: left;
  padding: 0 0 0 30px;
}

.nav_menu li a {
  text-decoration: none;
  display: block;
  color: #fff;
}
</style>